html {
  box-sizing: border-box;
  background: url("https://source.unsplash.com/NFs6dRTBgaM/2000x2000") 0 0 / cover fixed;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: sans-serif;
  font-size: 20px;
  display: flex;
}

.items {
  height: 800px;
  width: 100%;
  white-space: nowrap;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  will-change: transform;
  perspective: 500px;
  background: rgba(255, 255, 255, .1);
  border: 1px solid #fff;
  padding: 100px;
  font-size: 0;
  transition: all .2s;
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
  transform: scale(.98);
}

.items.active {
  cursor: grabbing;
  cursor: -webkit-grabbing;
  background: rgba(255, 255, 255, .3);
  transform: scale(1);
}

.item {
  width: 200px;
  height: calc(100% - 40px);
  color: rgba(0, 0, 0, .15);
  justify-content: center;
  align-items: center;
  font-size: 80px;
  font-weight: 100;
  display: inline-flex;
  box-shadow: inset 0 0 0 10px rgba(0, 0, 0, .15);
}

.item:nth-child(9n+1) {
  background: #1e90ff;
}

.item:nth-child(9n+2) {
  background: #daa520;
}

.item:nth-child(9n+3) {
  background: #afeeee;
}

.item:nth-child(9n+4) {
  background: gold;
}

.item:nth-child(9n+5) {
  background: #5f9ea0;
}

.item:nth-child(9n+6) {
  background: tomato;
}

.item:nth-child(9n+7) {
  background: #f08080;
}

.item:nth-child(9n+8) {
  background: #483d8b;
}

.item:nth-child(9n+9) {
  background: #639;
}

.item:nth-child(2n) {
  transform: scaleX(1.31)rotateY(40deg);
}

.item:nth-child(2n+1) {
  transform: scaleX(1.31)rotateY(-40deg);
}

